import { createRoot } from 'react-dom/client';
import './App.scss'
import FontFaceObserver from 'fontfaceobserver';
import { SlotContext } from './model/context';
import { detectAvif, detectWebp, utils } from 'pixi.js';
import { Languages } from './model/context/baseContext';

function App({ children }) {
  return (
    <div id="App" className='app-mod'>
      {children}
    </div>
  )
}

function detectWebglEnable(): Promise<any> {
  if (utils.isWebGLSupported()) {
    return Promise.resolve();
  } else {
    const text = {
      cs: "\nWebGL\n",
      kh: "\n  WebGL\n WebGL",
      id: "Loadin game gagal.\nBrowser Anda tidak mendukung atau telah menonaktifkan WebGL. \nLoad ulang game di browser dengan WebGL diaktifkan.",
      kr: "  .\n WebGL   .\nWebGL    .",
      th: "\n WebGL,\n ",
      vn: "Ti tr chi khng thnh cng.\nTrnh duyt ca bn khng h tr hoc  tt WebGL. \nVui lng ti li tr chi trong trnh duyt c bt WebGL."
    }[SlotContext.Obj.urlOptions.lang] ?? "Game loading failed. \nYour browser does not support or has disabled the WebGL. \nPlease reload game in browsers with WebGL enabled.";

    createRoot(document.body).render(
        <div className='webgl-not-support'>
          <div className='text'>{text}</div>
        </div>
    );
    return Promise.reject('webgl not support');
  }
}

function downloadFont(fontsName: string[] = []): Promise<void> {
  let completeFontCount: number = 0;
  return new Promise((resolve, reject) => {
    function func(count: number) {
      if (count == fontsName.length) {
        resolve();
      }
    }

    func(completeFontCount);
    for (let name of fontsName) {
      const font = new FontFaceObserver(name);
      font.load().then(() => {
        completeFontCount++;
        func(completeFontCount);
      });
    }
  });
}

function detectAssetsFormat(): Promise<any> {
  return Promise.all([detectAvif.test(), detectWebp.test()]).then(([isSupportAvif, isSupportWebp]) => {
    SlotContext.Obj.setSupportImagesFormat(isSupportWebp, isSupportAvif);

    const blurBg = document.getElementById('blur-bg');
    blurBg.className = blurBg.className + ' ' + SlotContext.Obj.filterImageFormat(['avif', 'webp', 'jpg']);
  });
}

export function Render(children, fontsName: string[] = []) {
  const context = SlotContext.Obj;
  const { operator, token, loginUrl, funPLay } = context.urlOptions;

  if (operator) {
    if (!funPLay && !token && loginUrl) {
      window.open(context.getLoginUrl(), '_self');
    } else {
      detectWebglEnable()
      .then(downloadFont.bind(null, fontsName))
      .then(detectAssetsFormat)
      .then(() => {
        createRoot(document.getElementById('root')).render(
          <App>{children}</App>
        );
      })
      .catch((e) => {
          console.error(e)
      });
    }
  } else {
    alert('The operator parameter is missing!');
  }
}
